import { useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'
import LogoText from './LogoText'
import NavButton from './NavButton'
import HeaderIcons from './HeaderIcons'
import dev from '../../Components/Media/dev.svg'
import logo1 from '../../Components/Media/logo1.png'
import bgimage from '../../Components/Media/bgimage.png'

function Header() {
  const location = useLocation();
  if (location.pathname == '/' || location.pathname == '/login' || location.pathname == '/register' || location.pathname == '/registerSuccess') {
    return
  }

  console.log('pathname at saf..', location.pathname.split('/'))


  return (
    <>
      {
        ((location.pathname != '/landing') && (location.pathname != '/error')) &&
        <div className="md:hidden grid grid-cols-12 w-100 px-0 h-16 bg-sky-700 shadow-xl  z-50 relative animate__animated animate__fadeInDown" >
          {/* LogoText contains municipal logo and text */}
          <div className="hidden sm:block col-span-0 sm:col-span-2"> <LogoText /></div>
          {/* NavButton contains action button to open or close Sidebar */}
          <div className='col-span-9 sm:col-span-3 place-items-center pl-6'><NavButton /></div>
          {/* HeaderIcons contains action icons */}
          <div className='col-span-3 place-items-center col-start-10'><HeaderIcons /></div>

        </div>
      }

      <div className={`hidden md:block  w-full ${location?.pathname?.split('/')[1]=='safform'?'h-[7vh]':'h-[20vh]'} z-150 relative`}>
        <div className='h-full  absolute top-0 left-0 right-0 opacity-40 w-1/2 mx-auto' style={{ backgroundImage: `url(${bgimage})`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat' }}> </div>
        <div className='w-full  h-full bg-black absolute top-0 left-0 opacity-70'> </div>

        {/* <div className='w-full  h-full bg-sky-700 absolute top-0 left-0 opacity-90'> </div> */}
        {location?.pathname?.split('/')[1] != 'safform' && <div className='absolute top-6 left-0 w-full h-full flex justify-center items-center'>
          <div className=''>
            <div className='text-white font-bold text-center text-[2vw] 2xl:pt-5 font-serif '><span className='px-4 2xl:py-2'>Jharkhand Urban Infrastructure Development Company</span></div>
            {/* <div className={`text-white text-center text-sm`}><div className='px-16 text-white mx-auto'>
              <span className='px-4'>JUDICO is Working for Urban Technology Imrprovement to ensure high Standard living</span>
              </div>
              </div> */}
            <img className='absolute 2xl:w-28 w-20 top-6 left-10' src={logo1} alt="" />
            <img className='absolute 2xl:w-60 w-48 top-7 right-4' src={dev} alt="" />
          </div>
        </div>}
      </div>

    </>
  )

  //.......................................v2.......................................
  // return (
  //   <>
  //     {
  //       ((location.pathname != '/landing') && (location.pathname != '/') && (location.pathname != '/error')) && <div className="flex w-100 h-16 bg-white shadow-xl  z-50 relative animate__animated animate__fadeInDown bg-red-600" >
  //         {/* LogoText contains municipal logo and text */}
  //         <div className="hidden sm:block col-span-0 sm:col-span-2"> <LogoText /></div>
  //         {/* NavButton contains action button to open or close Sidebar */}
  //         <div className='col-span-9 sm:col-span-3 place-items-center'><NavButton /></div>
  //         {/* HeaderIcons contains action icons */}
  //         <div className='col-span-3 place-items-center col-start-10 bg-blue-600 w-full'><HeaderIcons /></div>

  //       </div>
  //     }

  //   </>
  // )
}

export default Header
